define('ghost/mixins/settings-save', ['exports', 'ember'], function (exports, Ember) {

    'use strict';

    exports['default'] = Ember['default'].Mixin.create({
        submitting: false,

        actions: {
            save: function save() {
                var self = this;

                this.set('submitting', true);

                this.save().then(function () {
                    self.set('submitting', false);
                });
            }
        }
    });

});